@import "../../scss-rtl/variables";
@charset "UTF-8";
@media (min-width: 768px) {
	.app.sidenav-toggled {
		.app-content {
			margin-right: 0;
		}
		.app-sidebar {
			right: -230px;
		}
		.app-sidebar__overlay {
			visibility: hidden;
		}
	}
}
@media (max-width: 767px) {
	.app {
		overflow-x: hidden;
		.app-sidebar {
			right: -230px;
		}
		.app-sidebar__overlay {
			visibility: hidden;
		}
		&.sidenav-toggled {
			.app-content {
				margin-right: 0;
			}
			.app-sidebar {
				right: 0;
			}
			.app-sidebar__overlay {
				visibility: visible;
			}
		}
	}
}
.app-content {
	min-height: calc(100vh - 50px);
	margin-top: 50px;
	margin-bottom: 0 !important;
	-webkit-transition: margin-right 0.3s ease;
	-o-transition: margin-right 0.3s ease;
	transition: margin-right 0.3s ease;
	padding: 28px 30px 0 30px;
}
.footer .container {
	width: 100%;
	padding-right: 0.75rem;
	padding-left: 0.75rem;
	margin-right: auto;
	margin-left: auto;
}
@media (min-width: 768px) {
	.app-content {
		margin-right: 230px;
	}
}
@media (max-width: 767px) {
	.app-content {
		margin-top: 40px;
		min-width: 100%;
		padding: 20px 0.75rem 0 0.75rem;
	}
}
@media (max-width: 480px) {
	.app-content {
		padding: 15px;
	}
}
@media print {
	.app-content {
		margin: 0;
		padding: 0;
		background-color: $white;
	}
}
.light-text {
	font-weight: 300 !important;
}
.semibold-text {
	font-weight: 600 !important;
}
.line-head {
	padding-bottom: 10px;
	border-bottom: 1px solid #ddd;
}
/*----- Componant: Top Navigation Bar ----- */

.app-header {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	background-color: $primary;
	z-index: 10;
	padding-right: 15px;
	padding: 10px 0;
	border-bottom: 1px solid $white-1;
	.input-icon.mt-1 {
		margin-top: 2px !important;
	}
}
@media (min-width: 768px) {
	.app-header {
		padding-top: .75rem;
		padding-bottom: .75rem;
		padding-right: 10px;
	}
}
@media print {
	.app-header {
		display: none;
	}
}
.app-header__logo {
	-webkit-box-flex: 1;
	-ms-flex: 1 0 auto;
	flex: 1 0 auto;
	color: $white;
	text-align: center;
	padding: 0 15px;
	font-size: 26px;
	font-weight: 400;
	line-height: 50px;
	&:focus, &:hover {
		text-decoration: none;
	}
}
@media (min-width: 768px) {
	.app-header__logo {
		-webkit-box-flex: 0;
		-ms-flex: 0 0 auto;
		flex: 0 0 auto;
		display: block;
		width: 230px;
	}
}
.app-sidebar__toggle {
	font-family: fontAwesome;
	margin-top: 0px;
	line-height: 2.4;
	-webkit-transition: background-color 0.3s ease;
	-o-transition: background-color 0.3s ease;
	transition: background-color 0.3s ease;
	color: $white;
	&:before {
		content: "\e907";
		transition: all .25s;
		font-size: 21px;
		font-family: 'feather';
	}
	&:focus, &:hover {
		text-decoration: none;
		color: $white;
	}
}
@media (max-width: 767px) {
	.app-sidebar__toggle {
		-webkit-box-ordinal-group: 0;
		-ms-flex-order: -1;
		order: -1;
	}
}
.app-nav {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin-bottom: 0;
	-webkit-box-pack: end;
	-ms-flex-pack: end;
	justify-content: flex-end;
}
@media (min-width: 768px) {
	.app-nav {
		-webkit-box-flex: 1;
		-ms-flex: 1 0 auto;
		flex: 1 0 auto;
	}
}
.app-nav__item {
	display: block;
	padding: 15px;
	line-height: 20px;
	color: #3e4b5b;
	-webkit-transition: background-color 0.3s ease;
	-o-transition: background-color 0.3s ease;
	transition: background-color 0.3s ease;
	&:hover, &:focus {
		background: $black-1;
		color: #f6f6f6;
	}
}
.app-search {
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-item-align: center;
	align-self: center;
	margin-right: 15px;
	padding: 10px 0;
}
@media (max-width: 480px) {
	.app-search {
		display: none;
	}
}
.app-search__input {
	border: 0;
	padding: 5px 10px;
	padding-right: 30px;
	border-radius: 2px;
	background-color: $white-8;
	-webkit-transition: background-color 0.3s ease;
	-o-transition: background-color 0.3s ease;
	transition: background-color 0.3s ease;
	&::-webkit-input-placeholder, &:-ms-input-placeholder, &::-ms-input-placeholder, &::placeholder {
		color: rgba(0, 0, 0, 0.4);
	}
}
.app-search__button {
	position: absolute;
	right: 0;
	top: 10px;
	bottom: 10px;
	padding: 0 10px;
	border: 0;
	color: rgba(0, 0, 0, 0.8);
	background: none;
	cursor: pointer;
}
.app-notification {
	min-width: 270px;
}
.app-notification__title {
	padding: 8px 20px;
	text-align: center;
	background-color: rgba(0, 150, 136, 0.4);
	color: #333;
}
.app-notification__footer {
	padding: 8px 20px;
	text-align: center;
	background-color: #eee;
}
.app-notification__content {
	max-height: 220px;
	overflow-y: auto;
	&::-webkit-scrollbar {
		width: 6px;
	}
	&::-webkit-scrollbar-thumb {
		background: $black-2;
	}
}
.app-notification__item {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding: 8px 20px;
	color: inherit;
	border-bottom: 1px solid #ddd;
	-webkit-transition: background-color 0.3s ease;
	-o-transition: background-color 0.3s ease;
	transition: background-color 0.3s ease;
	&:focus, &:hover {
		color: inherit;
		text-decoration: none;
		background-color: #9493a9;
	}
}
.app-notification__message, .app-notification__meta {
	margin-bottom: 0;
}
.app-notification__icon {
	padding-right: 10px;
}
.app-notification__message {
	line-height: 1.2;
}
.app-sidebar {
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	padding-top: 75px;
	width: 230px;
	overflow: auto;
	background: $dark-theme;
	z-index: 1;
	box-shadow: 0 0 0 1px rgba(26, 27, 41, 0.12), 0 8px 16px 0 rgba(26, 27, 41, 0.24);
	color: $white;
	&::-webkit-scrollbar {
		width: 6px;
	}
	&::-webkit-scrollbar-thumb {
		background: $black-2;
	}
}
@media print {
	.app-sidebar {
		display: none;
	}
}
@media (max-width: 767px) {
	.app-sidebar__overlay {
		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		z-index: 9;
	}
}
.app-sidebar__user {
	padding: 12px 17px;
	color: #9493a9;
	border-bottom: 1px solid rgba(107, 122, 144, 0.1);
	display: inline-block;
	width: 100%;
	-webkit-box-align: center;
	align-items: center;
	.user-pro-body img {
		background: none;
		z-index: 1000;
		position: inherit;
		padding: 4px;
		width: 60px;
		height: 60px;
	}
}
.user-info h2 {
	font-size: 15px;
	margin: 0;
	font-weight: 600;
	margin-top: 0.8rem;
	color: $white;
}
.clearfix::after {
	display: block;
	clear: both;
	content: "";
}
.user-info span {
	font-size: 12px;
	line-height: 30px;
}
.profile-img {
	position: absolute;
	top: -1px;
	left: 43px;
	display: block;
	border: 1px solid rgba(101, 84, 187, 0.2);
	border-radius: 50%;
	font-size: 10px;
	line-height: 22px;
	width: 22px;
	text-align: center;
	height: 24px;
	color: $white;
	background: rgba(168, 172, 191, 0.2);
	&:hover {
		background: $white-6;
	}
}
.app-sidebar__user .user-pro-body {
	img {
		margin-bottom: 0 !important;
	}
	.u-dropdown {
		color: #97999f;
	}
	.user-info span.text-gray {
		color: $white !important;
	}
	.dropdown-menu {
		right: 0;
		width: 80%;
		left: 0;
		margin: 0 auto;
		-webkit-transform: none !important;
		-ms-transform: none !important;
		transform: none !important;
		top: 85px !important;
		li a {
			padding-left: 5px !important;
			padding-right: 10px !important;
			padding-top: 10px !important;
			padding: 10px !important;
		}
	}
}
.sidebar-mini.sidenav-toggled .app-sidebar {
	.user-info, .profile-img, .app-sidebar-footer {
		display: none;
	}
	.app-sidebar__user {
		padding: 20px 0 !important;
	}
	.user-pro-body img {
		width: 3rem;
		height: 3rem;
	}
	.sidebar-navs {
		display: none;
	}
}
@media (max-width: 767px) {
	.sidebar-mini.sidenav-toggled .app-sidebar {
		.user-info {
			display: block;
		}
		.profile-img {
			display: block;
			left: 36px;
		}
		.app-sidebar-footer {
			display: block;
		}
		.app-sidebar__user {
			padding: 20px 0 !important;
		}
	}
	.app-sidebar__user .user-pro-body img, .sidebar-mini.sidenav-toggled .app-sidebar .user-pro-body img {
		width: 3rem;
		height: 3rem;
	}
	.footer {
		padding: 1.25rem 0 !important;
	}
	.app-header {
		padding-top: .25rem;
		padding-bottom: .25rem;
	}
	.app-sidebar {
		z-index: 10;
		padding-top: 60px;
	}
	.app-header {
		z-index: 50;
	}
}
.app-sidebar__user-avatar {
	-webkit-box-flex: 0;
	-ms-flex: 0 0 auto;
	flex: 0 0 auto;
	margin-right: 15px;
}
.app-sidebar__user-name {
	&.text-sm {
		font-size: 13px;
	}
	font-size: 17px;
	line-height: 1.3;
	white-space: nowrap;
	overflow: hidden;
	-o-text-overflow: ellipsis;
	text-overflow: ellipsis;
	margin-bottom: 0;
}
.app-sidebar__user-designation {
	white-space: nowrap;
	overflow: hidden;
	-o-text-overflow: ellipsis;
	text-overflow: ellipsis;
	margin-bottom: 0;
}
.side-menu {
	margin-bottom: 0;
	padding-bottom: 40px;
	h3 {
		padding-right: 15px;
		color: $white;
		text-transform: uppercase;
		letter-spacing: .5px;
		font-size: 14px;
		margin-bottom: 1.5rem;
		margin-top: 1.5rem;
	}
	.slide li a {
		position: relative;
	}
}
.side-menu__item {
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	padding: 10px 35px 10px 30px;
	font-size: 15px;
	-webkit-transition: border-left-color 0.3s ease, background-color 0.3s ease;
	-o-transition: border-left-color 0.3s ease, background-color 0.3s ease;
	transition: border-left-color 0.3s ease, background-color 0.3s ease;
}
.app-sidebar .mCSB_draggerContainer {
	right: -11px;
}
.side-menu__icon {
	-webkit-box-flex: 0;
	-ms-flex: 0 0 auto;
	flex: 0 0 auto;
	font-size: 1.2rem;
	margin-left: 0.8rem;
}
.side-menu__label {
	white-space: nowrap;
	-webkit-box-flex: 1;
	-ms-flex: 1 1 auto;
	flex: 1 1 auto;
}
.app-sidebar .side-menu__item.active {
	border-top: 1px solid rgba(107, 122, 144, 0.1);
	border-bottom: 1px solid rgba(107, 122, 144, 0.1);
	&:hover {
		border-top: 1px solid rgba(107, 122, 144, 0.1);
		border-bottom: 1px solid rgba(107, 122, 144, 0.1);
	}
}
.slide.is-expanded [data-toggle='slide'] {
	border-bottom: 1px solid rgba(107, 122, 144, 0.1);
}
.side-menu li {
	a {
		border-top: 1px solid rgba(107, 122, 144, 0.1);
	}
	ul li a {
		border-top: 0;
		border-bottom: 0;
	}
}
.app-sidebar {
	.side-menu li a.active {
		.side-menu__icon {
			color: $white;
		}
		background: none;
	}
	.slide-menu li.active>a {
		font-weight: 500;
	}
	.side-menu li a:hover .side-menu__icon {
		color: $white;
	}
	.slide-menu li a:hover {
		background: $dark-theme;
	}
}
.side-menu li h3:hover {
	color: $white !important;
}
.slide-menu li {
	ul li a {
		font-size: 12px !important;
		font-weight: 300;
	}
	a {
		padding: 8px 45px 8px 10px;
		font-weight: 500;
	}
}
.side-menu .slide-menu li a {
	font-size: 13px;
}
.slide ul li a {
	color: $white;
	font-weight: 400;
}
.app-sidebar-footer {
	clear: both;
	display: block;
	color: $white;
	position: fixed;
	bottom: 0px;
	background: $white;
	width: 228px;
	z-index: 999;
	border-top: 1px solid rgba(107, 122, 144, 0.1);
	a {
		padding: 10px 0;
		text-align: center;
		width: 20%;
		color: $white;
		font-size: 15px;
		display: block;
		float: left;
		cursor: pointer;
		border-right: 1px solid rgba(107, 122, 144, 0.1);
		border-bottom: 1px solid rgba(107, 122, 144, 0.1);
		&:hover {
			background: #f8f9fa;
		}
	}
}
.slide ul ul a {
	font-size: 0.9em !important;
	padding: 10px 10px 10px 45px !important;
}
.app-sidebar ul li a {
	color: $white;
	font-weight: 400;
}
.slide.is-expanded {
	.slide-menu {
		display:block;
	}
	.angle {
		-webkit-transform: rotate(90deg);
		-ms-transform: rotate(90deg);
		transform: rotate(90deg);
	}
}
.slide-menu {
	display:none;
}
.slide-item {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	padding: 7px 7px 7px 45px;
	font-size: 1em;
	color: #3e4b5b;
	.icon {
		margin-right: 5px;
		transform: rotate(180deg);
	}
}
.angle {
	-webkit-transform-origin: center;
	-ms-transform-origin: center;
	transform-origin: center;
	-webkit-transition: -webkit-transform 0.3s ease;
	transition: -webkit-transform 0.3s ease;
	-o-transition: transform 0.3s ease;
	transition: transform 0.3s ease;
	transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}
@media (min-width: 768px) {
	.sidebar-mini.sidenav-toggled {
		.app-sidebar__user-name, .app-sidebar__user-designation, .angle {
			display: none;
		}
		.app-sidebar__user-avatar {
			width: 30px;
			height: 30px;
		}
		.app-content {
			margin-right: 70px;
		}
		.app-sidebar {
			right: 0;
			width: 70px;
			overflow: hidden;
			.side-menu__icon {
				margin: 0 auto;
			}
			&:hover {
				overflow: visible;
			}
		}
		.side-menu__item {
			overflow: hidden;
			padding: 10px 17px !important;
			&:hover {
				overflow: visible;
				.side-menu__label {
					opacity: 1;
				}
				+.slide-menu {
					visibility: visible;
				}
			}
		}
		.side-menu__label {
			display: block;
			position: absolute;
			top: 0;
			left: 50px;
			min-width: 180px;
			padding: 12px 5px 12px 20px;
			margin-left: -3px;
			line-height: 1;
			opacity: 0;
			background: $white;
			color: #3e4b5b;
			border-top-right-radius: 4px;
			border-bottom-right-radius: 4px;
		}
		.slide {
			&:hover {
				.side-menu__item {
					overflow: visible;
					border-left-color: #766cc5;
					background: #e0e2e8;
					color: #111;
				}
				.side-menu__label {
					opacity: 1;
				}
				.slide-menu {
					max-height: 100vh;
					opacity: 1;
					visibility: visible;
					z-index: 10;
				}
			}
			.side-menu__label {
				border-bottom-right-radius: 0;
			}
		}
		.slide-menu {
			position: absolute;
			left: 50px;
			min-width: 180px;
			padding: 12px 0;
			opacity: 0;
			border-bottom-right-radius: 4px;
			z-index: 9;
			visibility: hidden;
			-webkit-transition: visibility 0.3s ease;
			-o-transition: visibility 0.3s ease;
			transition: visibility 0.3s ease;
		}
		.app-sidebar .side-menu h3 {
			display: none;
		}
	}
}
.dropdown-menu {
	border-radius: 0;
	&.dropdown-menu-right {
		left: auto;
	}
}
.dropdown-item {
	.fa, .icon {
		vertical-align: middle;
	}
}
.btn {
	cursor: pointer;
	&:not([disabled]):not(.disabled):not(.btn-link) {
		&:hover, &:focus {
			text-decoration: none;
			-webkit-transform: translate3d(0, -1px, 0);
			transform: translate3d(0, -1px, 0);
			-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
			box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
		}
	}
	&[disabled] {
		cursor: not-allowed;
	}
	.icon, .fa {
		font-size: 14px;
		margin-right: 5px;
		vertical-align: middle;
	}
}
.app-title {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	-ms-flex-direction: row;
	flex-direction: row;
	background-color: #FFF;
	margin: -30px -30px 30px;
	padding: 20px 30px;
	-webkit-box-shadow: 0 1px 2px $black-1;
	box-shadow: 0 1px 2px $black-1;
	h1 {
		margin: 0;
		font-size: 24px;
		font-weight: 400;
	}
	p {
		margin-bottom: 0;
		font-style: italic;
	}
}
@media print {
	.app-title {
		display: none;
	}
}
@media (max-width: 480px) {
	.app-title {
		margin: -15px -15px 15px;
		padding: 20px;
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-ms-flex-direction: column;
		flex-direction: column;
		-webkit-box-align: start;
		-ms-flex-align: start;
		align-items: flex-start;
	}
}
@media (max-width: 480px) {
	.app-title p {
		display: none;
	}
}
.app-breadcrumb {
	margin-bottom: 0;
	text-align: right;
	font-weight: 500;
	font-size: 13px;
	text-transform: capitalize;
	padding: 0;
	text-align: left;
	padding: 0;
	background-color: transparent;
}
@media (max-width: 480px) {
	.app-breadcrumb {
		margin-top: 10px;
	}
}
.tile {
	position: relative;
	background: $white;
	border-radius: 3px;
	padding: 20px;
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px $black-2;
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px $black-2;
	margin-bottom: 30px;
	-webkit-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}
@media print {
	.tile {
		border: 1px solid #ddd;
	}
}
.sidenav-toggled .app-sidebar__user .avatar-md {
	width: 1.8rem;
	height: 1.8rem;
	line-height: 2rem;
	font-size: 1rem;
}
.app-header .header-brand {
	width: 200px;
}
.page-main:after {
	height: 230px;
}
.footer {
	padding: 1.25rem 230px 1.25rem 0px;
}
.sidebar-navs .nav-item a {
	border-radius: 3px;
	color: $white;
}
.side-menu {
	.slide li a:before {
		content: '\e049';
		margin-left: 8px;
		position: relative;
		font-family: 'typicons' !important;
